/* Orb Styling */
.orb {
  position: relative;
  height: 11rem;
  width: 11rem;
  overflow: hidden;
  border-radius: 9999px;
}
@media (min-width: 768px) {


  .orb {
    height: 12rem;
    width: 12rem;
  }
}
@media (min-width: 1024px) {


  .orb {
    height: 13rem;
    width: 13rem;
  }
}

.animate-orb {
    animation: wave 0.4s infinite ease-in-out;
}

.animate-orb-slow {
    animation: wave 2s infinite ease-in-out;
}

.orb-active {
    background: radial-gradient(circle at center, #c7c7c7, #908e8e, #595959);
}

.orb-inactive {
    background: radial-gradient(circle at center,
    rgba(200, 200, 200, 0.8),
    rgba(150, 150, 150, 0.6),
    rgba(100, 100, 100, 0.4));
}

/* Inner Gradient Layer for Waving Effect */
.orb::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    height: 200%;
    width: 200%;
    border-radius: 9999px;
    animation: wave-motion 6s infinite linear;
}

/* Keyframes for Waving Animation */
@keyframes wave {
    0%, 100% {
        transform: scale(1);
    }
    50% {
        transform: scale(1.05);
    }
}

@keyframes wave-motion {
    0% {
        transform: translate(0, 0) rotate(0deg);
    }
    50% {
        transform: translate(10px, 10px) rotate(180deg);
    }
    100% {
        transform: translate(0, 0) rotate(360deg);
    }
}
.scrollbar-hide::-webkit-scrollbar {
    display: none;
}

/* For IE, Edge and Firefox */
.scrollbar-hide {
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}
